.main__cont {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: #000;
}
.main__inner {
    font-size: 10vw;
    line-height: 0.85;
    font-family:"saol";
    color: #fff;
    text-transform: uppercase;
}

// main Animation
.main__inner > div {
    opacity: 0;
    transform: translateY(100px);

}